	@import url("../addOrder/addOrder.css");

	page {
		overflow: hidden;
		width: 100%;
		height: 100%;
		background-color: #f7f8f9;
	}

	.pageView {
		width: 100%;
		height: 70px;
	}

	.pageCell {
		margin-left: 12px;
		width: calc(50% - 26px);
		margin-top: 16px;
		height: 38px;
		border-radius: 4px;
		line-height: 38px;
		font-size: 16px;
		text-align: center;
		position: relative;
		overflow: hidden;
	}

	.page-image {
		position: absolute;
		width: 16px;
		height: 16px;
		right: 0px;
		bottom: 0px;
		/* background-color: #7900B2; */
	}

	.input-xuqiu {
		height: 107px;
		width: calc(100% - 24px);
		margin-left: 12px;
		font-size: 15px;
		line-height: 20px;
	}

	.input-xuqiu::after {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		height: 1px;
		margin: 0 auto;
		background-color: #f5f6f7;
	}
</style>
